<script>
import { Dialog } from 'vant';
import Fetch from './utils/fetch';
import qs from 'qs';

export default {
  name: 'App',
  data() {
    return {
      footer_type: 'n1',
      show_footer: false,
      show_type: 'index',
      search: {},
      zc_show: false,
      config: {},
    };
  },

  computed: {
    step1() {
      return this.$store.state.step.step || 0;
    },
  },
  created() {
    var type = localStorage.getItem('footer');
    if (type) {
      this.footer_type = type;
    }
    // 同意政策

    if (localStorage.getItem('zc') == null) {
      Fetch('/api/api/webconfig', { type: 'web' }).then(res => {
        this.config = res.data;
        this.zc_show = true;
      });
    }
  },
  beforeDestroy() {
    this.timer = null;
  },
  mounted() {
    this.syncDevNo();
    this.timer = setInterval(() => {
      this.syncDevNo();
    }, 5000);
    this.search = qs.parse(location.search.replace('?', '').replace('/', ''));
    console.log('search->', this.search);
    this.$store.commit('setStep', {
      today_step: 0,
      today_km: 0,
      today_kll: 0,
    });
  },
  methods: {
    cancelHandle() {
      this.zc_show = false;
    },
    confirmHandle() {
      localStorage.setItem('zc', 1);
    },
    //是否显示底部
    footer: function(bool, show = 'index') {
      this.show_footer = bool;
      this.show_type = show;
    },
    setFooterType: function(type = 'n1') {
      this.footer_type = type;
      localStorage.setItem('footer', type);
    },
    getFooterType: function() {
      return this.footer_type;
    },
    syncDevNo() {
      let devNo = localStorage.getItem('dev_no'),
        devType = localStorage.getItem('dev_type'),
        devOaid = localStorage.getItem('dev_oaid'),
        step = localStorage.getItem('step');
      Fetch(
        '/api/api/sync',
        {
          step: step,
          dev_no: devNo,
          dev_type: devType,
          dev_oaid: devOaid,
          channel: this.search.c,
        },
        null,
        false
      ).then(r => {
        this.footer_type = r.data.footer;
        if (r.data.is_open_notice_dialog) {
          this.openNoticeDialog();
        }
        this.$store.commit('setStep', r.data.step);
        console.log('上报成功 :>> ', r.data.step.today_step);
      });
    },
    openNoticeDialog() {
      Dialog.confirm({
        title: this.$t('lang.notice'),
        message: this.$t('lang.notice_title'),
      })
        .then(() => {
          console.log(456);
          this.$router.push('/notice');
        })
        .catch(() => {
          return;
        });
    },
  },
};
</script>
