import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store/';
import './registerServiceWorker';
import '@/assets/styles/reset.scss';
import { Notify, Dialog, Lazyload, NavBar, Toast } from 'vant';
import VueTouch from 'vue-touch';

import $ from 'jquery';
import requestAnimationFrameHack from './utils/requestAnimationFram';
import jequeryExtensFly from './utils/fly';
import SvgIcon from 'vue-svgicon';
import format from 'vue-text-format';
import '@/assets/styles/svg.scss';
import '@/assets/styles/common.scss';
import '@/components/icons';
import Fetch from '@/utils/fetch';
import VueI18n from 'vue-i18n';
import Clipboard from 'clipboard';
requestAnimationFrameHack();
jequeryExtensFly($);
window.$ = $;

Vue.prototype.$Fetch = Fetch;
Vue.prototype.$Dialog = Dialog;
Vue.prototype.$img = location.origin;
Vue.prototype.Clipboard = Clipboard;

Vue.use(Notify)
  .use(Dialog)
  .use(VueTouch, { name: 'v-touch' })
  .use(NavBar)
  .use(Toast)
  .use(Lazyload);
Vue.use(SvgIcon, {
  tagName: 'svgicon',
  isStroke: false,
});
Vue.use(VueI18n);
Vue.use(format);
Vue.config.productionTip = false;
Vue.config.devtools = true;
//判断系统语言
if (!localStorage.getItem('locale')) {
  localStorage.setItem('locale', 'yn');
}
const i18n = new VueI18n({
  locale: localStorage.getItem('locale') || 'yn', // 语言标识 //this.$i18n.locale // 通过切换locale的值来实现语言切换
  messages: {
    cn: require('../VueI18n/cn'),
    en: require('../VueI18n/en'),
    yn: require('../VueI18n/yn'),
  },
});
new Vue({
  router,
  store,
  i18n,
  render: h => h(App),
}).$mount('#app');
